import React from 'react';

type Props = {
  preparedListOfGoods: string[],
};

export const GoodsList: React.FC<Props> = ({ preparedListOfGoods }) => {
  return (
    <ul className="list">
      {preparedListOfGoods.map(good => (
        <li
          key={good}
          className="list__item"
        >
          {good}
        </li>
      ))}
    </ul>
  );
};
